@use 'base/mixins' as m;
@use 'base/variables' as v;
@use 'sass:color';

.hero {
    position: relative;
    max-width: 100vw;
    min-height: 50rem;
    display: flex;
    justify-content: center;
    align-items: center;

    &__background {
        position: absolute;
        inset: 0;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
        z-index: -1;
    }

    &__background-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: 50% 80%;
    }

    &__overlay {
        position: absolute;
        top: 0;
        right: 0;
        width: 40%;
        height: 100%;
        background-color: rgba(v.$black, .5);

        @include m.phone {
            width: 50%;
        }
    }

    &__content {
        width: 80%;
        height: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-right: 2.14rem;

        @include m.phone {
            margin-right: auto;
        }
    }

    &__title {
        font-size: v.$h3-font-size;
        color: v.$white;
        font-weight: 500;
        /* text-transform: uppercase; */
        line-height: 4.5rem;
        
        @include m.phone {
            font-size: v.$h3-font-size-mobile;
            line-height: 3.5rem;
            text-align: center;
        }
    }

    &__subtitle {
        font-size: v.$h4-font-size;
        color: v.$white;
        line-height: 4.5rem;
        font-weight: 600;
        margin-top: 2.5rem;

        @include m.phone {
            font-size: v.$h4-font-size-mobile;
            line-height: 4.5rem;
            text-align: center;
        }
    }

    &__button {
        margin-top: 2.5rem;
        width: 25rem;
        @include m.boton(v.$yellow-2, v.$black);
    }
}